.App-logo {
  -webkit-animation: App-logo-spin infinite 4s alternate;
          animation: App-logo-spin infinite 4s alternate;
  height: 8rem;
}

.header {
  background-color: rgb(187, 187, 187);
  padding: 20px;
  color: rgb(68, 50, 50);
  font-size: 3rem;
}

.App-title {
  font-size: 3rem;
  margin: 3rem;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

nav {
  background-color: #ccc;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

nav a:visited {
  color: #000;
}

nav .navWide {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin-left: 5rem;
  font-size: 1.5rem;
  font-family: 'Oswald', sans-serif;
}

nav .navWide a {
  text-decoration: none;
  padding: 0 1rem;
  color: #000;
}

nav .navWide a:hover {
  color: #422;
  padding: .7rem;
  border-radius: 4px;
  background-color: rgb(255, 255, 255);
}


